.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: teal;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.success {
  display: inline-block;
  background: #090;
  width: 30%;
  padding: 0.6rem;
  font-style: italic;
  font-size: 0.8rem;
}

.error {
  display: inline-block;
  background: chocolate;
  width: 30%;
  padding: 0.6rem;
  font-style: italic;
  font-size: 0.8rem;
}

.App-link {
  font-size: 0.8rem;
  margin-top: 2rem;
}
.App-link a {
  text-decoration: none;
  color: rgb(10, 218, 10);
  font-weight: bold;
}
