body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

pre {
  font-family: monospace, cursive;
  font-size: 0.7rem;
  padding: 0.5rem;
}
.login {
  width: 30%;
  margin: 0 auto;
}
.login input {
  display: block;
  margin: 1rem auto;
  width: 80%;
  height: 20px;
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 1.3rem;
  font-style: italic;
}

.login button {
  display: block;
  width: 80%;
  margin: auto;
  color: #090;
  font-size: 1.5rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
}

.logout {
  width: 30%;
  font-weight: normal;
  padding: 0.2rem;
  height: 30px;
  background: chocolate;
  border: none;
  border-radius: 5px;
  color: rgb(224, 194, 171);
}
button:hover {
  cursor: pointer;
  background: #f2f2f2;
}

.register {
  width: 100%;
}

.register p {
  font-size: 1rem;
  text-align: center;
  margin: 25px 0;
}
.register input {
  height: 20px;
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 1.3rem;
  font-style: italic;
  margin-left: 15px;
}

.register button {
  border: none;
  border-radius: 5px;
  background: #090;
  height: 50px;
  width: 25%;
  font-size: 1.3rem;
  margin: 2rem 0;
  color: #fff;
}
